<template>
  <b-row class="mb-1">
    <b-col style="text-align: left">
      <b-form>
        <b-col>
          <b-alert variant="secondary" show align="center"
            ><h3>회원가입</h3></b-alert
          >
        </b-col>
        <b-form-group id="userid-group" label="ID" label-for="userid">
          <b-form-input
            id="userid"
            v-model="user.userid"
            type="text"
            required
            placeholder="아이디 입력"
            @blur="checkDuplicate"
          ></b-form-input>
          <span class="badge badge-danger mt-1" v-if="!availableID"
            >이미 사용중인 아이디입니다.</span
          >
          <span class="badge badge-danger mt-1" v-if="!availableIDForm"
            >아이디는 4자이상 12자 이하만 가능합니다.</span
          >
        </b-form-group>
        <b-form-group id="userpwd-group" label="비밀번호" label-for="userpwd">
          <b-form-input
            id="userpwd"
            v-model="user.userpwd"
            type="password"
            required
            placeholder="비밀번호 입력"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="username-group" label="이름" label-for="username">
          <b-form-input
            id="username"
            v-model="user.username"
            type="text"
            required
            placeholder="이름 입력"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="email-group" label="이메일" label-for="email">
          <b-form-input
            id="email"
            v-model="user.email"
            type="text"
            required
            placeholder="이메일 입력"
          ></b-form-input>
        </b-form-group>

        <b-button variant="primary" class="m-1" @click="onSubmit"
          >가입</b-button
        >
        <b-button variant="danger" class="m-1" @click="moveMain"
          >메인으로</b-button
        >
      </b-form>
    </b-col>
  </b-row>
</template>

<script>
import { register, checkId } from "@/api/member";

export default {
  name: "MemberRegister",
  data() {
    return {
      user: {
        userid: "",
        username: "",
        userpwd: "",
        email: "",
        joindate: "",
      },
      availableID: true,
      availableIDForm: true,
    };
  },
  methods: {
    async checkDuplicate() {
      this.availableIDForm = true;
      this.availableID = true;
      if (!this.validateID()) {
        this.availableIDForm = false;
        return;
      } else {
        this.availableEmailIDForm = true;
      }

      await checkId(
        this.user.userid,
        (response) => {
          console.log(response.data);
          if (response.data) {
            this.availableID = false;
          } else {
            this.availableID = true;
          }
          console.log(this.availableID);
        },
        (error) => {
          console.log("아이디 중복체크 중 에러 발생", error);
        },
      );
    },
    validateID() {
      if (this.user.userid.length >= 4 && this.user.userid.length <= 12) {
        return true;
      } else {
        return false;
      }
    },
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";
      !this.user.userid && ((msg = "아이디를 입력해주세요"), (err = false)),
        err &&
          !this.user.userpwd &&
          ((msg = "비밀번호를 입력해주세요"), (err = false)),
        err &&
          !this.user.username &&
          ((msg = "이름을 입력해주세요"), (err = false)),
        err &&
          !this.user.email &&
          ((msg = "이메일을 입력해주세요"), (err = false));
      if (!err) alert(msg);
      else this.registMember();
    },
    registMember() {
      register(
        {
          userid: this.user.userid,
          username: this.user.username,
          userpwd: this.user.userpwd,
          email: this.user.email,
        },
        ({ data }) => {
          let msg = "회원가입시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "회원가입이 완료되었습니다.";
          }
          alert(msg);
          this.moveMain();
        },
        (error) => {
          console.log(error);
        },
      );
    },
    moveMain() {
      this.$router.push({ name: "home" });
    },
  },
};
</script>

<style>
.container {
  background-color: rgba(255, 255, 255, 0);
}
